import React from "react";
import { auth } from "../firebase";
import "./logout.css";

const style = {
  button: `bg-gray-200 px-4 py-2 hover:bg-gray-100`,
};

const LogOut = () => {
  const signOut = () => {
    signOut(auth);
  };

  return (
    <div className="log">
      <button onClick={() => auth.signOut()} className={style.button}>
        Logout
      </button>
    </div>
  );
};

export default LogOut;
